import uniSwiperDor from "../../components/uni-swiper-dot/uni-swiper-dot.vue";
import mixPulldownRefresh from "../../components/mix-refresh/mix-pulldown-refresh.vue";
import json from "../../json.js";
let windowWidth = 0,scollTimer = false,tabBar;
export default {
	components:{
		uniSwiperDor,
		mixPulldownRefresh
	},
	data() {
		return {
		  tabCurrentIndex:0,
		  tabBar:[],
		  tabList:[{
			  "name":"财经",
			  "id":"1"
		  },{
			  "name":"情感",
			  "id":"2"
		  },{
			  "name":"娱乐",
			  "id":"3"
		  },{
			  "name":"游戏",
			  "id":"4"
		  },{
			  "name":"情感",
			  "id":"5"
		  },{
			  "name":"娱乐",
			  "id":"6"
		  },{
			  "name":"游戏",
			  "id":"7"
		  },{
			  "name":"情感",
			  "id":"8"
		  },{
			  "name":"娱乐",
			  "id":"9"
		  },{
			  "name":"游戏",
			  "id":"10"
		  }],
		  scrollLeft:0,
		  enableScroll:false
		}
	},
	onLoad() {

	},
	methods: {
	
		getElSize(id){
			return new Promise((res,rej) =>{
				let ul = uni.createSelectorQuery().select("#"+id);
				ul.fields({
					scrollOffset:true,
					size:true,
					rect:true
				},(data)=>{
					res(data);
				}).exec();
			});	
		},   
		loadTabBar(){
			let tabBar = json.tabList;
			tabBar.forEach(item=>{
				item.loadStatus = 0;
				item.newsList = [];
				item.refresh = false;
			})
			this.tabBar = tabBar;
			this.loadMore()
		},
		async changeTab(e){
			let index = e;
			let width = 0;
			this.tabCurrentIndex = index;
			let nowWidth;
			this.tabBar =await this.getElSize('navBar');
			if(scollTimer){
				clearTimeout(scollTimer);
				scollTimer = false;
			}
			for(let i = 0; i<=index ;i++){
				let result = await this.getElSize('tab'+i);
				width +=result.width;
				if(i == index){
					nowWidth = result.width;
				}			
			}
			scollTimer = setTimeout(() =>{
				let position = width - nowWidth/2 - windowWidth/2;
				if(position > 0){
					this.scrollLeft = width - nowWidth/2 - windowWidth/2;
				}else{
					this.scrollLeft = 0;
				}
				this.tabCurrentIndex = index;
			},300)						
		},
		loadNewsList(type){
			let tabItem = this.tabBar[this.tabCurrentIndex]
			if(type == 'add'){
				if(tabItem.loadStatus == 2){
					return
				}
				tabItem.loadStatus = 1;
			}
			if(type == 'refresh'){
				tabItem.refresh = true
			}
			setTimeout(function(){
				let newsList = json.newsList;
				if(type == 'refresh'){
					tabItem.newsList=[];
				}
				newsList.forEach(item=>{
					item.id = Math.random()*10000;
					tabItem.newsList.push(item)
				})
			},600)
			
		},
		setEnableScroll(enable){
			if(this.enableScroll != enable){
				this.enableScroll = enable;
			}	
		},
		loadMore(){
			this.loadNewsList('add');
		}
	},
	async onLoad(){
		windowWidth = uni.getSystemInfoSync().windowWidth;
		this.loadTabBar();
	}
}
